
body {
    font-family: "Open Sans", sans-serif;
}

#board {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

#header {
    margin-bottom: 20px; 
    text-align: center;
}

h1 {
    font-family: "Pacifico", cursive;
    font-size: 3rem;
    margin: 0;
}

#header > h2 {
    display: inline;
    margin: 0 10px;
}

.score-board {
    display: flex;
    justify-content: center;
    gap: 25px;
}

.imageCard {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    text-align: center;
}

.imgContainer {
    border: solid 5px black;
    border-radius: 10px;
    justify-self: center;
    width: 200px;
    height: 200px;
    overflow: hidden;
}

.imageCardImg {
    width: 100%;
}

@media (max-width: 1060px) {
    #board {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 815px) {
    #board {
        grid-template-columns: 1fr 1fr;
    }

    .imgContainer {
        width: 250px;
        height: 250px;
    }
}

@media (max-width: 550px) {
    #board {
        grid-template-columns: repeat(2, 1fr);
    }

    .imgContainer {
        width: 145px;
        height: 145px;
    }
}
